/* Tcalculator.css */
body {
    background: rgba(0, 0, 0, 0.8); /* Dark semi-transparent background */
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    margin: 0;
    font-family: Arial, sans-serif;
  }
  
  .calc {
    background: rgba(255, 255, 255, 0.1); /* Semi-transparent calculator background */
    border: 1px solid rgba(255, 255, 255, 0.25);
    border-radius: 16px;
    padding: 20px;
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.2);
    backdrop-filter: blur(10px); /* Glassmorphism effect */
    text-align: center;
    width: 300px;
  }
  
  .inp {
    width: 90%;
    height: 40px;
    margin-bottom: 10px;
    border: none;
    border-radius: 8px;
    text-align: right;
    padding-right: 10px;
    font-size: 1.2em;
    background-color: rgba(255, 255, 255, 0.8);
  }
  
  .nbt {
    width: 60px;
    height: 60px;
    margin: 5px;
    border: none;
    border-radius: 8px;
    font-size: 1.2em;
    background-color: rgba(255, 255, 255, 0.8);
    cursor: pointer;
    transition: background 0.3s, transform 0.2s;
  }
  
  .nbt:hover {
    background-color: rgba(0, 150, 255, 0.8);
    transform: scale(1.1);
  }
  
  .nbt:active {
    transform: scale(0.95);
  }
  